/* Mobile layout styles. */
/* First section */
#hero {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 24px;
  padding-top: 12px; }

#hero-header {
  margin-top: 0;
  margin-bottom: 24px; }
  #hero-header .name {
    font-family: 'Share Tech Mono', monospace; }
  #hero-header .behind {
    font-size: 75%;
    opacity: 0.75;
    display: inline-block;
    margin-bottom: 12px; }

#hero-image {
  max-width: 500px;
  width: 100%;
  display: inline-block; }

#hero-quote {
  margin: 8px 0 0 0;
  display: inline-block;
  line-height: 150%;
  color: #757575; }
  #hero-quote .quote span:first-of-type {
    font-size: 20px;
    color: #616161; }
  #hero-quote img {
    position: relative;
    top: 4px; }
  #hero-quote .quoter {
    display: inline-block;
    margin-top: 12px;
    font-size: 16px; }

.team-number {
  padding: 4px 8px;
  border-radius: 6px;
  background: #43A047;
  color: #fafafa;
  font-family: 'Share Tech Mono', monospace; }

@media (min-width: 800px) {
  .item-container {
    background: rgba(20, 20, 20, 0.5); } }

#gallery {
  position: relative;
  overflow: hidden;
  padding-top: 36px;
  padding-bottom: 12px;
  /* Gallery item caption */
  /* Gallery item */ }
  #gallery .bg-shape {
    position: absolute;
    top: 8px;
    left: -80px;
    right: -80px;
    bottom: 7px;
    background: #1B5E20;
    z-index: -1;
    -webkit-transform: rotate(1.5deg);
        -ms-transform: rotate(1.5deg);
            transform: rotate(1.5deg); }
  #gallery .item-caption {
    margin: 8px 0 24px 0;
    font-weight: 300;
    color: rgba(250, 250, 250, 0.85);
    font-size: 18px;
    padding: 0 24px; }
  #gallery .item {
    width: 100%;
    max-width: 800px;
    background-attachment: fixed;
    background-repeat: no-repeat; }

@media (min-width: 880px) {
  #gallery .bg-shape {
    -webkit-transform: rotate(1deg);
        -ms-transform: rotate(1deg);
            transform: rotate(1deg); } }

#video-container {
  display: inline-block;
  width: 100%;
  text-align: center;
  background: rgba(20, 20, 20, 0.15); }
  #video-container > * {
    width: 100vw;
    max-width: 1024px;
    height: calc(100vw * (9 / 16));
    max-height: 576px; }

#we-subheading {
  margin: 24px 0 8px 0; }

#we-like {
  list-style: none;
  padding: 0;
  width: 240px;
  margin: 0 auto;
  text-align: left; }
  #we-like .text {
    font-size: 18px; }
  #we-like .check {
    width: 36px;
    height: 36px;
    display: inline-block;
    background: url(../assets/ic_check.png);
    background-repeat: no-repeat;
    background-size: contain;
    position: relative;
    top: 10px;
    margin-right: 8px; }

/* Hidden in mobile */
#facebook-feed {
  display: none; }

/* Medium size devices. Should show facebook feed. */
@media (min-width: 768px) {
  #hero {
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 50% 50%;
    grid-template-areas: 'header header' 'left right'; }
  #hero-header {
    grid-area: header; }
  #left-side {
    grid-column: 1;
    grid-row: 2; }
  #facebook-feed {
    grid-row: 2;
    grid-column: 2;
    display: initial; }
    #facebook-feed .task-indicator {
      position: relative;
      top: 24px; } }

#fb-link {
  color: #3b5998;
  text-decoration: none; }
  #fb-link img {
    position: relative;
    top: 4px; }



