/* Subheading up top. */
/* List of sponsors. */
#sponsor-list {
  padding: 0;
  margin: 0;
  list-style: none;
  overflow: hidden;
  /* Wraps background shape and sponsor info. */ }
  #sponsor-list li {
    margin-bottom: 48px; }
  #sponsor-list .shape-container {
    position: relative;
    padding-bottom: 12px;
    /* Sponsor name */ }
    #sponsor-list .shape-container .logo {
      width: 100%;
      height: 280px;
      background-color: white;
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat; }
    #sponsor-list .shape-container .bg-shape {
      position: absolute;
      top: 48px;
      right: -24px;
      left: -24px;
      bottom: 12px;
      z-index: -1;
      -webkit-transform: rotate(1.5deg);
          -ms-transform: rotate(1.5deg);
              transform: rotate(1.5deg);
      -webkit-box-shadow: 0 0 24px rgba(20, 20, 20, 0.15);
              box-shadow: 0 0 24px rgba(20, 20, 20, 0.15); }
    #sponsor-list .shape-container .name {
      margin: 0;
      padding: 10px 0 12px 0;
      color: #fafafa; }
    #sponsor-list .shape-container .description {
      color: rgba(250, 250, 250, 0.75);
      margin: 0 auto;
      padding: 0 24px 24px 24px;
      max-width: 560px; }

/* Display items as a grid */
@media (min-width: 992px) {
  #sponsor-list {
    display: grid;
    max-width: 1200px;
    margin: 0 auto;
    grid-template-columns: auto auto; }
    #sponsor-list li:nth-child(odd) .bg-shape {
      right: 0; }
    #sponsor-list li:nth-child(even) .bg-shape {
      left: 0; } }



