#team-list {
  /* Member picture */
  list-style: none;
  padding: 0; }
  #team-list .member {
    display: grid;
    margin-bottom: 36px;
    grid-template-rows: auto;
    grid-template-columns: 120px auto;
    grid-column-gap: 18px;
    text-align: left; }
    #team-list .member div {
      grid-row: 1; }
    #team-list .member .picture {
      grid-column: 1;
      width: 120px;
      height: 120px;
      border-radius: 50%;
      display: inline-block;
      background-size: cover; }
    #team-list .member .name {
      margin: 8px 0 0 0;
      white-space: nowrap;
      font-family: 'Share Tech Mono', monospace; }
    #team-list .member .role {
      margin: 0; }
      #team-list .member .role .icon {
        width: 24px;
        height: 24px;
        margin-right: 6px;
        background-size: cover;
        display: inline-block;
        position: relative;
        top: 6px; }
    #team-list .member .email-link {
      background: #2E7D32;
      padding: 4px 8px;
      border-radius: 4px;
      position: relative;
      top: 4px;
      padding-top: 2px;
      margin-top: 8px;
      display: inline-block;
      color: #fafafa;
      font-size: 15px;
      text-decoration: none; }
    #team-list .member:nth-child(even) {
      grid-template-columns: auto 120px;
      text-align: right; }
      #team-list .member:nth-child(even) .picture {
        grid-column: 2; }
      #team-list .member:nth-child(even) .about {
        grid-column: 1; }

/* Start diplaying members in a grid. */
@media (min-width: 768px) {
  #team-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, 320px);
    grid-template-rows: repeat(auto-fill, 250px);
    max-width: 960px;
    margin: 0 auto; }
    #team-list .member {
      text-align: center !important;
      display: block; } }



