#global-footer {
  position: relative;
  overflow: hidden; }
  #global-footer .bg-shape {
    position: absolute;
    top: 8px;
    left: -8px;
    right: -8px;
    bottom: -8px;
    -webkit-transform: rotate(-1.5deg);
        -ms-transform: rotate(-1.5deg);
            transform: rotate(-1.5deg);
    z-index: -1;
    background-color: #56378e; }

@media (min-width: 650px) {
  #global-footer .bg-shape {
    -webkit-transform: rotate(-1deg);
        -ms-transform: rotate(-1deg);
            transform: rotate(-1deg); } }

#footer-nav ul {
  list-style: none;
  padding: 24px 0; }
  #footer-nav ul li {
    color: #fafafa;
    padding: 12px; }
    #footer-nav ul li a {
      text-decoration: none;
      padding: 0 12px;
      padding-bottom: 8px;
      color: #fafafa;
      border-bottom: 2px solid rgba(250, 250, 250, 0.5);
      border-radius: 2px; }
    #footer-nav ul li .fb-logo {
      width: 24px;
      height: 24px;
      margin-right: 12px;
      background: url(../assets/fb_logo.png);
      background-repeat: no-repeat;
      background-size: contain;
      display: inline-block;
      position: relative;
      top: 6px; }



