/* Mobile styles for global header. */
#global-nav {
  position: relative;
  overflow: hidden;
  text-align: center;
  padding-bottom: 24px; }
  #global-nav .bg-shape {
    position: absolute;
    left: -80px;
    top: -80px;
    right: -80px;
    bottom: 24px;
    background: #2E7D32;
    -webkit-box-shadow: 0 0 12px rgba(20, 20, 20, 0.1);
            box-shadow: 0 0 12px rgba(20, 20, 20, 0.1);
    z-index: -1;
    -webkit-transform: rotate(-2.5deg);
        -ms-transform: rotate(-2.5deg);
            transform: rotate(-2.5deg); }
  #global-nav ul {
    margin: 0;
    padding: 12px 0 8px 0; }
    #global-nav ul li {
      position: relative;
      font-family: 'Share Tech Mono', monospace;
      display: inline-block;
      padding: 12px;
      font-size: 18px; }
      #global-nav ul li,
      #global-nav ul li a {
        color: #fafafa;
        text-decoration: none; }
      #global-nav ul li a {
        -webkit-transition: color 250ms cubic-bezier(0.215, 0.61, 0.355, 1);
        -o-transition: color 250ms cubic-bezier(0.215, 0.61, 0.355, 1);
        transition: color 250ms cubic-bezier(0.215, 0.61, 0.355, 1); }
        #global-nav ul li a:hover {
          color: rgba(250, 250, 250, 0.75); }
      #global-nav ul li .selected-effect {
        display: none; }
      #global-nav ul li.active,
      #global-nav ul li.active a {
        color: #2E7D32; }
      #global-nav ul li.active .selected-effect {
        z-index: -1;
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: -28px;
        background: #fafafa;
        display: initial;
        border-radius: 8px; }

/* Medium size devices */
@media (min-width: 768px) {
  #global-nav .bg-shape {
    background-image: url(../assets/viking_sillhouette.png);
    background-repeat: no-repeat;
    background-position: 120px 76px;
    background-size: auto;
    -webkit-transform: rotate(-1.25deg);
        -ms-transform: rotate(-1.25deg);
            transform: rotate(-1.25deg); } }



